.infos-post{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    padding: 16px;
}
.infos-post span{
    color: '#8e8e8e';
    display: flex;
    align-items: center;
}
.infos-post .user-elements{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 8px;
}
.infos-post .user-elements div{
    width: 42px;
    height: 42px;
}
.infos-post .user-elements div img{
    width: 38px;
    height: 38px;
}
.infos-post .user-elements span, .post-footer .likes, .post-footer .profile {
    font-weight: 700;
}
.profile-caption{
    display: flex;
    gap: 5px;
}
.infos-post .user-elements span:hover{
    opacity: 0.65;
}
.post-content img{
    width: 100%;
}
.post-footer{
    padding: 8px 16px;
}
.post-reactions{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.post-reactions svg:hover, .add-comment svg:hover{
    cursor: pointer;
    opacity: 0.55;
}
.comments span:hover, .likes span:hover, .post-footer .profile:hover{
    cursor: pointer;
}
.post-footer-left{
    display: flex;
    gap: 8px;
}
.post-footer .messageCircle{
    transform: scaleX(-1);   
}
.post-footer .add-comment
{
    display: flex;
    justify-content: space-between;
}
.post-footer .add-comment, .post-footer .comments {
    color: var(--secondary);
}
.add-comment {
    margin-top: 8px;
}