.container {
    padding: 15px;
    display: flex;  
    height: 120px; 
    position: relative;
  }
  
  .user-container {
    display: flex;
    gap: 16px;
    white-space: nowrap;
    transition: transform 0.3s ease;
    font-size: 0; /* Para remover espa√ßo extra entre os elementos */
    overflow-x: scroll;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* IE and Edge */
  }
  
  .user-container::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera */
  }
  
  .story-elements {
    cursor: pointer;
  }
  
  .user-elements div {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(#c82d8e, #f99d4c);
  }
  
  .user-elements {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .image-user-story {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    border: 2px solid var(--background);
  }
  
  .user-elements span {
    font-size: 13px;
    white-space: nowrap;
  }
  
  .nextIcon, .prevIcon {
    position: absolute;
    display: flex;
    background-color: var(--background);
    width: 24px;
    height: 24px;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
  }
  .nextIcon{
    right: 0;
  }
  .prevIcon {
    left: 0;
  }

  .hidden {
    display: none;
  }  