.MainGrid {
    display: grid;
    grid-template-areas: "firstColumn secondColumn";
    grid-template-columns: 614px 320px;

    grid-gap: 50px;

    max-width: 957px;
    margin: 80px auto;
}

.box {
    background-color: var(--background);
    border: 1px solid var(--border);
}
.post-container{
    border-top: 1px solid var(--border);
}

.User .img-user{
    width: 56px;
    height: 56px;
    border-radius: 50%;
}
.User {
    display: flex;
    gap: 16px;
    align-items: center;
    position: relative;
    margin: 24px 0px 12px;
}
.User span{
    display: block;
}
.username{
    color: var(--primary);
    font-weight: 650;
}
.name{
    color: var(--secondary);
}
.User .change-user{
    right: 0;
    position: absolute;
    font-weight: 650;
}
.User .change-user span{
    color: var(--quaternary);
}
.User .change-user span:hover{
    cursor: pointer;
}
.suggestions-header{
    display: flex;
    justify-content: space-between;
}
.suggestions span {
    color: var(--secondary);
    font-weight: 500;
}
.suggestions .show-all-suggestions{
    color: var(--primary);
}
.suggestions-list
{
    margin: 20px 0;
}
.suggestions .suggestion-elements{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 8px 0;
}
.suggestions .user-elements div img {
    width: 32px;
    height: 32px;
}
.suggestions .user-elements div{
    background: none;
}
.suggestions .user-elements{
    display: flex;
    flex-direction: row;
    justify-content: left;
    gap: 8px;
}
.suggestions .image-user-story{
    border: none;
}
.suggestions .user-data{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.suggestions .user-data span{
    color: var(--primary);
    font-weight: 650;
}
.suggestions .user-data .follow-status{
    color: var(--secondary);
    font-weight: 400;
}
.suggestions-list .user-elements div{
    width: auto;
    height: auto;
}
.suggestion-elements .follow .follow-link{
    color: var(--quaternary);
    font-size: 13px;
}
.suggestion-elements .follow .follow-link:hover{
    cursor: pointer;
    color: var(--quinternary);
}
.suggestions-footer
{
    margin: 20px 0;
    display: flex;
    flex-wrap: wrap;
}
.suggestions-footer span
{
    font-size: 12px;
    color: var(--tertiary);
}
.suggestions-footer span:hover
{
    cursor: pointer;
    text-decoration: underline;
}
.suggestions-footer svg
{
    font-size: 12px;
}
.copyright span
{
    font-size: 12px;
    color: var(--tertiary);
    font-weight: 400;
}